.enter_page{
	position: relative;
	overflow: hidden;
	display: flex;
	align-items:center;
	justify-content: center;
	.soc_icons{
		position: absolute;
		width: 100%;
		bottom: 7.5%;
		left: 0;
		display: flex;
		justify-content:center;
		.circle {
			margin: 0 4%;
		}
	}
}
.enter_page,.map_animation{
	width: 100vw;
	height: 100vh;
}
.form_enter{
	--form_enter_width_local: calc(var(--form_enter_width) / 2);
	--form_enter_height_local: calc(var(--form_enter_height) / 2);
	width: var(--form_enter_width_local);
	height: var(--form_enter_height_local);
	background: var(--form_enter_bg) no-repeat 0 0;
	background-size: cover;
	// padding: 8.5% 3% 0;
	// padding: 160px 3% 0;
	padding: 121px 3% 0;
	position: relative;
	.alert_block{
		height: 50px;
		overflow: hidden;
		padding: 5px;
		display: flex;
		align-items:center;
		justify-content:center;
		text-align: center;
		visibility: hidden;
		&.active{
			visibility: visible;
		}
		p{
			margin: 0;
			font-size: 15px;
		}
	}
	.back_btn {
		right: auto;
		left: 27px;
		top: 113px;
	}
	.area_form{
		display: none;
		// position: relative;
		&.active{
			display: block;
		}
		.result_message{
			padding: 10px;
			height: 270px;
			// background: rgba(red,.3);
		}
	}
}
.map_prev{
		// padding: 200px;
		// max-width: 90vw;
		// max-height: 90vh;
		// overflow: hidden;
		// max-width: 1000px;
	.panel_holder{
		padding: calc(var(--size_border_local));
		// max-width: 90vw;
		max-width: 1000px;
		max-height: 90vh;
		overflow: hidden;
		img{
			width: 100%;
			height: 100%;
			object-fit:cover;
		}
	}
	.circle{
		bottom: 20px;
		right: 20px;
		z-index: 2;
	}
}
.server_prev{
	width: 100%;
	max-width: 997px;
}
.live_search{
	position: relative;
}
// anim
.map_animation{
	position: absolute;
	bottom: 0;
	left:50%;
	transform:translateX(-50%);
	overflow: hidden;
	background: rgba(red,.4);
	z-index: -1;
	user-select: none;
	min-width: $body_min_width;
	height: calc(100vw * 0.56);
	@media (max-aspect-ratio: 16/9) {
		height: 100vh;
		width:  calc(100vh * 1.777);
	}
	.bg{
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
	.left_mask,.right_mask{
		img,video{
			// max-width: 100%;
			// display: block;
			// height: auto;
			width: 100%;
			height: 100%;
			object-fit:cover;
			position: absolute;
			top: 50%;
			left: 50%;
			transform:translate(-50%,-50%);
		}
	}
	.left_mask{
		position: absolute;
		bottom: 24%;
		// left: 15%;
		left: 13%;
		background: green;
		// width: 32%;
		width: 37%;
		height: 37%;
		mask: url(/images/map/enter_page/left_mask.png)  0 0  no-repeat;
		mask-size: 100% 100%;
	}
	.right_mask{
		position: absolute;
		// top: 30%;
		top: 26%;
		// right: 17%;
		right: 13%;
		background: red;
		background:url(/images/map/enter_page/right_mask.png) no-repeat;
		// width: 30.5%;
		width: 35%;
		height: 37%;
		mask: url(/images/map/enter_page/right_mask.png)  0 0  no-repeat;
		mask-size: 100% 100%;
	}
}